* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  text-align: center;
  background-color: #f4f4f4;
  padding: 20px;
}

header {
  color: #fff;
  background-color: #6200ea;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 15px;
  font-size: 1.8rem;
  display: flex;
}

#book-form-section {
  background: #fff;
  border-radius: 10px;
  max-width: 400px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

form input, form select, form button {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
}

form button {
  color: #fff;
  cursor: pointer;
  background-color: #6200ea;
  border: none;
  font-size: 1rem;
}

form button:hover {
  background-color: #3700b3;
}

#search-section {
  margin: 20px 0;
}

#search-section input, #search-section select {
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 45%;
  margin: 5px;
  padding: 10px;
}

#book-list-section {
  background: #fff;
  border-radius: 10px;
  max-width: 600px;
  margin: auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

ul#bookList {
  margin-top: 10px;
  list-style: none;
}

ul#bookList li {
  background: #fff;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 15px;
  display: flex;
  box-shadow: 0 2px 4px #0000001a;
}

ul#bookList li span {
  font-weight: bold;
}

.edit-btn, .delete-btn {
  color: #fff;
  cursor: pointer;
  background-color: #6200ea;
  border: none;
  border-radius: 4px;
  margin: 4px;
  padding: 8px 12px;
  font-size: 1rem;
  transition: background-color .3s, transform .2s;
}

.delete-btn {
  background: #d32f2f;
}

.delete-btn:hover {
  background: #b71c1c;
}

@media (width <= 600px) {
  #search-section input, #search-section select {
    width: 100%;
    margin-bottom: 10px;
  }

  ul#bookList li {
    flex-direction: column;
    align-items: flex-start;
  }

  .edit-btn, .delete-btn {
    width: 100%;
    margin: 4px 0;
    padding: 10px;
    font-size: .9rem;
    display: block;
  }

  header {
    flex-direction: column;
    align-items: center;
  }

  header nav ul {
    flex-direction: column;
    width: 100%;
  }

  header nav ul li {
    text-align: center;
    width: 100%;
    margin: 5px 0;
  }

  header h1 {
    font-size: 1.5rem;
  }

  header nav ul li a {
    width: 100%;
    display: block;
  }
}

select {
  cursor: pointer;
  appearance: none;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  font-size: 1rem;
}

select::-ms-expand {
  display: none;
}

select:focus {
  border-color: #6200ea;
  outline: none;
  box-shadow: 0 0 5px #6200ea80;
}

option {
  color: #000;
  background-color: #fff;
  padding: 10px;
  font-size: 1rem;
}

option:hover {
  color: #fff;
  background-color: #6200ea;
}

nav ul {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  margin: 0;
}

nav ul li a {
  color: #fff;
  padding: 8px 12px;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
}

nav ul li a:hover {
  background-color: #3700b3;
  border-radius: 4px;
}

section {
  padding: 20px;
}

.modal {
  z-index: 100;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
  border-radius: 8px;
  width: 80%;
  max-width: 500px;
  margin: 10% auto;
  padding: 20px;
}

.close-btn {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

.close-btn:hover, .close-btn:focus {
  color: #000;
  text-decoration: none;
}
